const transition = { type: 'spring', stiffness: 200, damping: 50 }

const Line = posed.div({
  enter: {
    opacity: 1,
    transition: { duration: 500 },
  },
  exit: {
    opacity: 0,
    transition: { duration: 500 },
  },
})

const Line1 = posed.div({
  enter: {
    opacity: 1,
    x: 0,
    transition,
  },
  exit: {
    opacity: 0,
    x: ({ m }) => m,
    transition,
  },
})

